export default "import React from 'react';\r\nimport {Table} from 'react-bootstrap';\r\n\r\nconst Responsive = () => {\r\n  return (\r\n    <div style={{width: '100%'}}>\r\n      <Table responsive>\r\n        <thead>\r\n          <tr>\r\n            <th>#</th>\r\n            {Array.from({length: 12}).map((_, index) => (\r\n              <th key={index}>Table heading</th>\r\n            ))}\r\n          </tr>\r\n        </thead>\r\n        <tbody>\r\n          <tr>\r\n            <td>1</td>\r\n            {Array.from({length: 12}).map((_, index) => (\r\n              <td key={index}>Table cell {index}</td>\r\n            ))}\r\n          </tr>\r\n          <tr>\r\n            <td>2</td>\r\n            {Array.from({length: 12}).map((_, index) => (\r\n              <td key={index}>Table cell {index}</td>\r\n            ))}\r\n          </tr>\r\n          <tr>\r\n            <td>3</td>\r\n            {Array.from({length: 12}).map((_, index) => (\r\n              <td key={index}>Table cell {index}</td>\r\n            ))}\r\n          </tr>\r\n        </tbody>\r\n      </Table>\r\n    </div>\r\n  );\r\n};\r\n\r\nexport default Responsive;\r\n";